.header__container {
  max-width: 880px;
  min-height: 119px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
}

@media screen and (max-width: 1023px) {
  .header {
    max-width: 720px;
  }
}

@media screen and (max-width: 767px) {
  .header__container {
    max-width: 260px;
  }
}
